import React from "react";

import {
  Timeline,
  TimelineItem
} from "Core/UI/Components/Itemization/Timeline";

export default class TimelineWorkExperience extends React.Component {
  render() {
    return (
      <Timeline>
        <TimelineItem
          heading="Greater Than AB"
          subheading="Full-stack utvecklare"
          tagline="2018"
          footer={[
            {
              label: "Bransch",
              content: "InsurTech"
            },
            {
              label: "Plats",
              content: "Stockholm"
            },
            {
              label: "Referenser",
              content: "P begran"
            }
          ]}
        >
          Blev rekryterad av Greater Than i mitten av 2018, anstlldes som
          full-stack utvecklare av webb- och mobilapplikationer och
          -systemlsningar.
        </TimelineItem>

        <TimelineItem
          heading="Egenfretagare"
          subheading="NC NetCommando AB"
          tagline="2016"
        >
          Drev egen firma fr.o.m. sena 2015 under mitt egna namn, hsten 2016
          grundade jag (ej ensam) ett aktiebolag med namnet{" "}
          <span className="text-muted" title="Detta r nnu inte en lnk.">
            NC NetCommando AB
          </span>
          , som gick i konkurs ett r senare (september, 2017).
        </TimelineItem>

        <TimelineItem
          heading="ProPublic AB"
          subheading="Systemansvarig, webbutvecklare"
          tagline="2013"
        >
          Drev webbutveckling och -drift t denna konsultfirma som startade sin
          verksamhet vid rsskiftet 2013-2014. Hr hade jag mjlighet att under
          flexibla omstndigheter utveckla mina komptenser inom omrdet.
        </TimelineItem>
      </Timeline>
    );
  }
}



